    border: 2px solid #fff;
    cursor: pointer;
    width: 100% !important;
    padding-left: 15px !important;
    padding-right: 15px !important;
}
#Picture-RWD{
    display: none ;
}
    .lingalleryContainer[data-v-40681078] .lingallery .lingallery_thumbnails .lingallery_thumbnails_content .lingallery_thumbnails_content_elem img {
        border: 2px
     solid #fff;
        cursor: pointer;
        width: 100%;
