        padding-left: 20px;
        max-width: 100% !important;
        margin-left: 0;
        padding-right: 20px;
    }
    element.style {
        touch-action: pan-y;
        user-select: none;
        -webkit-user-drag: none;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        padding-top: 30px;
    }
    
.lingalleryContainer[data-v-40681078] .lingallery .lingallery_thumbnails .lingallery_thumbnails_content .lingallery_thumbnails_content_elem img {
    border: 2px
 solid #fff;
    cursor: pointer;
    width: 100%;
    padding: 5px;
    padding-left: 20px;
    max-width: 100% !important;
    margin-left: 0;
    padding-right: 30px ;

}
.lingalleryContainer[data-v-40681078] .lingallery .lingallery_thumbnails .lingallery_thumbnails_content .lingallery_thumbnails_content_elem img {
    border: 2px
 solid #fff;
    cursor: pointer;
    width: 100%;
    padding: 5px;
    padding-left: 10px;
    max-width: 100% !important;
    margin-left: 0;
    padding-right: 10px;
}
.lingalleryContainer[data-v-40681078] .lingallery .lingallery_thumbnails {
    overflow-x: auto;
    width: 100%;
    -ms-scroll-snap-type: x mandatory;
    scroll-snap-type: x mandatory;
    padding-left: 80px;
}




@import "../../../assets/helpers/RWD.scss";

</style>



